<template>

  <div>
    <h3>政策信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col>
        <Form :model="formData" :label-width="100">
          <div style="width: 50%">
            <FormItem label="政策标题">
              <Input v-model="formData.title" placeholder="请填写政策标题"></Input>
            </FormItem>
            <FormItem label="政策简介">
              <Input v-model="formData.content" placeholder="请填写政策简介"></Input>
            </FormItem>

            <FormItem label="状态">
              <RadioGroup v-model="formData.status">
                <Radio :label="0"><span>不可用</span></Radio>
                <Radio :label="1"><span>可用</span></Radio>
              </RadioGroup>
            </FormItem>
            <FormItem label="封面图">
              <ImgSelect v-model="picture" :url="formData.picture">
              </ImgSelect>
            </FormItem>
          </div>

          <FormItem>
            <rich-view v-model="formData.rich_text"></rich-view>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {
        status: 1
      },
      picture: null,
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
  },
  methods: {
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
      }, () => {
        this.load = false
      })
    },
    submit () {
      let data = this.$formUtil.objToFormData(this.formData)
      if (this.picture == null && !this.$formUtil.isStrValid(this.formData.picture)) {
        this.$Message.error('请选择封面截图')
        return
      } else if (this.picture != null) {
        data.append('picture', this.picture)
      }

      this.load = true
      api.save(data, () => {
        this.load = false
        this.$router.push({ name: 'policy' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

